.title{width:100%;font-size:18px;text-align:center;font-weight:700;padding-bottom:10px;margin-bottom:10px;box-shadow:0 2px 1px #f4f4fc}.buttons{display:flex;justify-content:space-between}.buttons__post{font-weight:700;background-color:#33a99a;color:#fff;padding:10px 30px;border-radius:10px;font-size:16px}.buttons__post:hover{background-color:#278276}.buttons__reset{height:30px;width:30px;font-weight:700;background-color:rgba(0,0,0,0);border-radius:50%}.buttons__reset svg{margin-top:4px;width:20px;height:20px}.buttons__reset svg path{fill:#33a99a;stroke:#33a99a}.buttons__reset:hover{background-color:#d4f4f4}*{box-sizing:border-box;margin:0;padding:0;font-family:Arial,Helvetica,sans-serif}a{text-decoration:none;color:#000}button{border:none;outline:none;user-select:none;cursor:pointer}input{outline:none;border:none}ul,ol{list-style:none}html,body{height:100%}#root{height:100%;overflow:hidden}.wrapper{height:100%;overflow:auto}.wrapper::-webkit-scrollbar{width:16px;z-index:200}.wrapper::-webkit-scrollbar-track{background:#d4f4f4}.wrapper::-webkit-scrollbar-thumb{background:#33a99a;border-radius:10px;border:4px solid #d4f4f4}.wrapper::-webkit-scrollbar-thumb:hover{background:#278276}.wrapper .content{min-height:100%;display:flex;flex-direction:column;position:relative;margin-left:100px;margin-right:20px}.wrapper .fullscreen{height:100%;display:flex;flex-direction:column}.wrapper main{position:relative;flex:1 1 auto}.container{width:1045px;margin:0 auto}@media screen and (max-width: 1080px){.container{width:auto;margin:0 10px}}.blue-btn{background-color:#445fe7;border-radius:5px;color:#fff;transition:.1s;border:1px solid #445fe7}.blue-btn:hover{background:#2040e2;color:#fff}.blue-btn--outline{background-color:#fff;color:#445fe7;border:1px solid #445fe7}.blue-btn--outline:hover{background-color:#6e81e3;color:#fff}.red-btn{background-color:#cf2e2f;border-radius:4px;transition:.1s;color:#fff;border:1px solid #cf2e2f}.red-btn:hover{background:#ae2727}.red-btn--outline{background-color:#fff;color:#cf2e2f;border:1px solid #cf2e2f}.red-btn--outline:hover{background-color:#cf2e2f;color:#fff}.toast-button{color:#888;padding:0 10px;font-size:16px;height:20px;background-color:rgba(0,0,0,0)}.toast-button:hover{color:#445fe7;text-decoration:underline}.toast-body svg{width:40px}.cancel-button{border-radius:50%;width:20px;height:20px;transition:.3s;color:#888;background-color:#fff}.cancel-button::before{content:"✖"}.cancel-button:hover{background-color:#c4c4c4}.cancel-button:hover::before{color:#fff}.circular{animation:rotate 1s linear infinite;width:100px;height:100px;border-radius:50%;background-color:red;position:relative}.circular::before{content:"";position:absolute;width:80px;height:80px;background-color:#fff;top:10px;left:10px;border-radius:50%;z-index:1}.circular::after{content:"";position:absolute;width:104px;height:104px;top:-2px;left:-2px;border-radius:50%;z-index:1;background-image:linear-gradient(white, rgba(255, 255, 255, 0.9333333333), rgba(255, 255, 255, 0))}.circular__center{position:absolute;top:-1px;left:-1px;width:102px;height:102px;border-radius:50%;border:50px solid #fff;border-right:50px solid blue}@keyframes rotate{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}